.content {
  text-align: center;
}

.title {
  border: 1px solid lightgray;
  width: 500px;
  margin: 15px auto;
  border-radius: 10px;
  padding: 4px 0;
}
.a {
  display: block;
  color: tomato;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}
.a:hover {
  background-color: beige;
}
.movie_img {
  border-radius: 10px;
}

.list {
  list-style: none;
  display: inline-block;
  margin: 0 10px;
  color: gray;
}
