.content {
  text-align: center;
}

.poster {
  border-radius: 15px;
}
.download {
  text-align: center;
  margin: 10px auto;
  text-decoration: none;
  border: none;
  background-color: wheat;
}
.link {
  text-decoration: none;
  color: blanchedalmond;
}
/* CSS */
.download {
  background: #ff4742;
  border: 1px solid #ff4742;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 16px;
  min-height: 40px;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
}

.download:hover,
.download:active {
  background-color: whitesmoke;
  background-position: 0 0;
  color: #ff4742;
}

.download:active {
  opacity: 0.5;
}
.info {
  padding: 10px;
  margin: 10px;
  color: tomato;
}
